.app-wrap {
  height: 100vh;
  box-sizing: border-box;
  > .custom-layout {
    height: 100%;
    .site-layout {
      height: 100%;
      .site-layout-header{
        background: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .trigger {
          flex-shrink: 0;
          padding: 0 24px;
          font-size: 18px;
          line-height: 64px;
          cursor: pointer;
          transition: color 0.3s;
        }
        
        .trigger:hover {
          color: #1890ff;
        }
        .right {
          .custom-label {
            padding: 0 24px;
            font-size: 18px;
          }
          .custom-label:hover {
            color: #1890ff;
          }
        }
      }
      .site-layout-content {
        box-sizing: border-box;
        height: calc(100% - 64px);
        padding: 24px 20px;
        overflow-y: auto;
        > div {
          background: #fff;
          border-radius: 12px;
        }
      }
    }
  }
  
  .logo {
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    .data {
      padding-right: 8px;
    }
    .label {
      color: #ffb100;
    }
    .img {
      width: 100%;
      height: auto;
    }
  }
}
