.task-wrap {
  .success {
    color: #52c41a;
  }
  .warning {
    color: #faad14;
  }
  .operate-tool {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    > span {
      cursor: pointer;
    }
  }
  .custom-btn-color1 {
    color: #409EFF;
  }
  .custom-btn-color2 {
    color: rgb(103, 194, 58);
  }
  .custom-btn-color3 {
    color: rgb(230, 162, 60);
  }
  .custom-btn-color4 {
    color: rgb(144, 147, 153);
  }
  .ant-table-cell {
    word-break: break-all;
  }
}
.custom-descriptions {
  padding: 20px;
  border-radius: 12px;
  border: solid 1px #eee;
  margin-bottom: 25px;
  .success {
    color: #52c41a;
  }
  .warning {
    color: #faad14;
  }
}
.custom-title {
  font-size: 16px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
  padding-bottom: 20px;
}
.jsoneditor {
  margin-bottom: 20px;
}
.custom-footer {
  text-align: right;
  .ant-btn {
    margin-left: 20px;
  }
}