.plugin-wrap {
  padding: 20px;
  .tool-wrap {
    display: flex;
    justify-content: space-between;
    padding-bottom: 12px;
    > .left {
      flex-grow: 1;
      display: flex;
      .back {
        cursor: pointer;
        color: #1890ff;
      }
      .custom-input {
        width: 18%;
        min-width: 80px;
        margin-right: 20px;
      }
    }
    > .right {
      flex-shrink: 0;
    }
  }
  .list-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    > .item {
      margin: 20px 20px;
      width: 300px;
      height: 200px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: solid 1px #f0f0f0;
      border-radius: 12px;
      cursor: pointer;
      > .inner {
        text-align: center;
        > .img {
          width: 100px;
        }
        > .img-tip {
          width: 100px;
          height: 80px;
          border-radius: 12px;
          border: solid 1px #eee;
          font-weight: bold;
          font-size: 24px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        > .title {
          padding-top: 10px;
        }
      }
    }
    > .item:hover {
      border: solid 1px #1890ff;
    }
  }
}
.custom-tip {
  padding-top: 10px;
  color: #999;
}