.cluster-wrap {
  padding: 10px 0;
  .header-wrap {
    padding: 0 20px 0 0;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    .custom-select {
      width: 200px;
    }
  }
  .custom-tab {
    .ant-tabs-nav {
      padding-left: 20px;
    }
    .ant-tabs-content-holder {
      padding: 0 20px 0 20px;
    }
    .tool-wrap {
      padding-bottom: 12px;
      display: flex;
      flex-direction: row-reverse;
    }
    .operate-tool {
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      > span {
        cursor: pointer;
      }
    }
    .custom-btn-color1 {
      color: #409EFF;
    }
    .custom-btn-color2 {
      color: rgb(103, 194, 58);
    }
    .custom-btn-color3 {
      color: rgb(230, 162, 60);
    }
    .custom-btn-color4 {
      color: rgb(144, 147, 153);
    }
  }
  .ant-table-cell {
    word-break: break-all;
  }
  
}